.Breadcrumbs {
  display: flex;
  align-items: center;

  &__icon {
    padding: 2px 0 0;
  }

  &__link {
    text-decoration: none;
  }

  &__active {
    color: $gray-color;
  }

  &__link-image {
    width: 10px;
    height: 10px;
    object-fit: cover;
    margin: auto 10px;
  }
}

.wrap__container {
  display: flex;
  align-items: center;
  padding: 25px 0;
  height: 50px;
}
